import React, { useState, useEffect } from 'react';
import { v4 as uuidv4 } from 'uuid'
import { randomColor } from 'randomcolor'
import Draggable from 'react-draggable'
import './App.css';


function App() {

  const[item, setItem] = useState('')
  const[items, setItems] = useState(
    JSON.parse(localStorage.getItem('items')) || []
  )

    useEffect(() => {
      localStorage.setItem('items', JSON.stringify(items))
    }, [items])
    
    const newItem = () =>{
      if(item.trim() !== ''){
        const newItem = {
          id:uuidv4(),
          item,
          color:randomColor(
            {
              luminosity: 'light'
            }
          ),
          defaultPos:{
            x: 70,
            y: -734
          }
        }

        setItems((items) => [...items, newItem])
        setItem('')

      }else{
        alert("Enter something");
        setItem('')
      }
    }

    const deleteNode= (id) =>{
      setItems(items.filter((item) => item.id !== id))
    }

    const updatePos = (data, index) =>{
      let newArray = [...items]
      newArray[index].defaultPos = {x:data.x, y: data.y}
      setItems(newArray)
    }

    const keyPress = (e) => {
      const code = e.keyCode || e.which
      if( code === 13 ){
        newItem()
      }
    }


    

  return (
    
    <div className="App">
      <div className='wrapper'>
        <input placeholder={`Покушать`} 
        value={item}
        onChange={(e) => setItem(e.target.value)} 
        onKeyPress={(e) => keyPress(e)}
        type="text"/>
        <button className="enter" onClick={newItem}>Создать задачу</button>
      
      </div>
     
  {
    items.map((item, index)=>{
      return(
        <Draggable 
          key={index}
          defaultPosition={item.defaultPos}
          onStop={(_, data) => {
            updatePos(data, index)
          }}
        >
          <div className="todo__item" style={{backgroundColor:item.color}}>
           <a> {`${item.item}`}</a><hr id="debug"></hr>
            <button id="delete" className="delete"
              onClick={()=>deleteNode(item.id)}
              onTouchStart={()=>deleteNode(item.id)}
            >
              X
            </button>
            <br></br>
         <a id="debug" >   {`ID: ${item.id}`}</a>
          <br id="debug"></br>
           <a id="debug"> {`posX: ${item.defaultPos.x} | posY: ${item.defaultPos.y}`}</a>
          </div>
          
        </Draggable>

   
      )
    })
  }

    </div>
    
  );
  
}

export default App;
